document.getElementById('selectFolderBtn').addEventListener('click', async () => {
    try {
        const folderHandle = await window.showDirectoryPicker();

        // Ê∞∏Á∂öÂåñ„Éà„Éº„ÇØ„É≥„Çí‰øùÂ≠òÔºàserialize ‰∏çÂèØ„Å™„ÅÆ„Åß IDB API„Åß„É©„ÉÉ„ÉóÂøÖË¶ÅÔºâ
        const granted = await verifyPermission(folderHandle);
        if (!granted) {
            alert("„Éï„Ç©„É´„ÉÄ„Å∏„ÅÆ„Ç¢„ÇØ„Çª„ÇπÊ®©Èôê„ÅåË®±ÂèØ„Åï„Çå„Åæ„Åõ„Çì„Åß„Åó„Åü„ÄÇ");
            return;
        }

        // „Éï„Ç©„É´„ÉÄÂêç„ÅÆ„Åø„Çí‰øùÂ≠òÔºà„Éè„É≥„Éâ„É´„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Åß„ÅØ„Å™„ÅèÔºâ
        const folderInfo = {
            name: folderHandle.name,
            timestamp: Date.now()
        };

        await chrome.storage.local.set({ folderInfo });

        // „Ç∞„É≠„Éº„Éê„É´Â§âÊï∞„Å®„Åó„Å¶„Éè„É≥„Éâ„É´„Çí‰øùÊåÅ
        window.currentFolderHandle = folderHandle;

        document.getElementById('folderResult').textContent = `‚úÖ ÈÅ∏ÊäûÊ∏à„Åø: ${folderHandle.name}`;

        // ÂÆüË°å„Éú„Çø„É≥„ÇíÊúâÂäπÂåñ
        document.getElementById('executeBtn').disabled = false;

    } catch (err) {
        if (err.name === 'AbortError') {
            // „É¶„Éº„Ç∂„Éº„Åå„Ç≠„É£„É≥„Çª„É´„Åó„ÅüÂ†¥Âêà
            console.log("„Éï„Ç©„É´„ÉÄÈÅ∏Êäû„Åå„Ç≠„É£„É≥„Çª„É´„Åï„Çå„Åæ„Åó„Åü");
            document.getElementById('folderResult').textContent = 'üìÅ Êú™ÈÅ∏ÊäûÔºà„Ç≠„É£„É≥„Çª„É´„Åï„Çå„Åæ„Åó„ÅüÔºâ';
        } else {
            // „Åù„ÅÆ‰ªñ„ÅÆ„Ç®„É©„Éº
            console.error("„Éï„Ç©„É´„ÉÄÈÅ∏Êäû„Å´Â§±Êïó:", err);
            document.getElementById('folderResult').textContent = '‚ùå ÈÅ∏ÊäûÂ§±Êïó: ' + err.message;
        }
    }
});

// „Éö„Éº„Ç∏Ë™≠„ÅøËæº„ÅøÊôÇ„Å´‰øùÂ≠ò„Åï„Çå„ÅüÊÉÖÂ†±„ÇíÂæ©ÂÖÉ
document.addEventListener('DOMContentLoaded', async () => {
    try {
        const result = await chrome.storage.local.get(['folderInfo']);
        if (result.folderInfo) {
            document.getElementById('folderResult').textContent =
                `üìÅ ÂâçÂõûÈÅ∏Êäû: ${result.folderInfo.name} (‚ÄªÂÜçÈÅ∏Êäû„ÅåÂøÖË¶Å)`;
        }
    } catch (err) {
        console.error("‰øùÂ≠ò„Éá„Éº„Çø„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó:", err);
    }
});

async function verifyPermission(handle) {
    const options = { mode: 'readwrite' };
    if ((await handle.queryPermission(options)) === 'granted') return true;
    if ((await handle.requestPermission(options)) === 'granted') return true;
    return false;
}